



























export default {
    name: 'neil-modal',
    props: {
        title: { //标题
            type: String,
            default: ''
        },
        content: String, //提示的内容
        align: { //content 的对齐方式left/center/right
            type: String,
            default: 'left'
        },
        cancelText: { //取消按钮的文字，默认为"取消"
            type: String,
            default: '取消'
        },
        cancelColor: { //取消按钮颜色
            type: String,
            default: '#333333'
        },
        confirmText: { //确定按钮的文字，默认为"确定"
            type: String,
            default: '确定'
        },
        confirmColor: { //确认按钮颜色
            type: String,
            default: '#007aff'
        },
        showCancel: { //是否显示取消按钮，默认为 true
            type: [Boolean, String],
            default: true
        },
        show: { //是否显示模态框
            type: [Boolean, String],
            default: false
        }
    },
    data() {
        return {
            isOpen: false,
            showAnimation:false,
            inWatchTimeout:false,
            inMethodsTimeout:false
        }
    },
    watch: {
        show(val) {
            if(this.inWatchTimeout){
                return
            }
            if (val) {
            	this.isOpen = val
                setTimeout(() => {
                	this.showAnimation = val
                }, 50)
            } else {
                this.showAnimation = val
                this.inWatchTimeout = true
            	setTimeout(() => {
            		this.isOpen = val
                    this.inWatchTimeout = false
            	}, 200)
            }
        }
    },
    created() {
    	this.isOpen = this.show
    	setTimeout(() => {
    		this.showAnimation = this.show
    	}, 50)
    },
    methods: {
        bindTouchmove() {},
        clickLeft() {
            setTimeout(() => {
            	this.$emit('cancel')
            }, 200)
            this.closeModal()
        },
        clickRight() {
            setTimeout(() => {
            	this.$emit('confirm')
            }, 200)
            this.closeModal()
        },
        closeModal() {
            this.showAnimation = false
            this.inMethodsTimeout = true
            setTimeout(() => {
            	this.isOpen = false
                this.inMethodsTimeout = false
                this.$emit('close')
            }, 200)
        }
    }
}
